
.user-page {
  background-color: #284a70;
  padding: 20px 0px;
  margin-bottom: -0.5rem;
}
.user-page .category {
  position: relative;
  text-align: center;
  margin: 0 20px;
}
.user-page .category span {
  display: inline-block;
  padding: 0 30px;
  font-size: .3rem;
  position: relative;
  z-index: 2;
  background-color: #284a70;
  color: #fff;
}
.user-page .category:after {
  content: '';
  position: absolute;
  height: 1px;
  background-color: #fff;
  width: 100%;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 1;
}
.user-page .user-group {
  margin: 0 40px;
}
.user-page .user-group .m-cell a.cell-item,
.user-page .user-group .m-cell label.cell-item,
.user-page .user-group .m-cell {
  background-color: transparent;
}
.user-page .user-group .cell-left {
  color: #fff;
}
.user-page .user-group .cell-item:not(:last-child):after,
.user-page .user-group .m-cell:after {
  display: none;
}



