
#account .tab-nav-item,
#account .tab-nav-item.tab-active,
#account .tab-nav-item.tab-active:active {
  background-color: transparent ;
  color: #266dbf;
}
#account .account-body {
  padding: 0 50px;
}
#account .account-btn {
  padding-top: 30px;
  text-align: center;
}
#account .tab-panel-item {
  padding-top: 20px;
}
#account .flexbox-item {
  padding: 10px 0;
}
#account .m-input {
  padding: 8px 0;
  border-bottom: 1px solid #000;
}
#account .tab-nav:after {
  border-bottom: none;
}
#account .btn-primary {
  background-color: #266dbf;
}
#account p {
  line-height: 2;
  text-align: center;
}



