/*icon样式*/
[class*="iconfont"]:before {
  font-family: "iconfont" !important;
}
/*navabar底部线*/
.navbar-bottom-line-color:after {
  border: none!important;
}
/*滑动容器底部空白*/
/*.g-scrollview:after{
  display: none;
}*/
/*背景颜色*/
.tab-panel,
.m-accordion {
  background-color: #e9eaec;
}
/*改变折叠图标*/
.accordion-title > i {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.accordion-title > i:after {
  height: auto;
  width: auto;
  border: none;
  content: "\e673";
}
.m-list {
  background-color: transparent;
}
.list-item {
  position: relative;
}
/*tab*/
/*去掉中间分隔线*/
.tab-nav-item:not(:last-child):after {
  display: none;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
html,
body {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  background-color: #e9eaec;
}
a {
  color: inherit;
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
ul {
  list-style: none;
}
.responsive-img {
  width: 100%;
}
.cover-bg {
  background: no-repeat center;
  background-size: cover;
  min-height: 60px;
  min-width: 80px;
  position: relative;
}
.text-right {
  text-align: right;
}
.text-center {
  text-align: center;
}
.flexbox-h,
.flexbox-v {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.flexbox-h > *,
.flexbox-v > * {
  position: relative;
}
.flexbox-h .flex,
.flexbox-v .flex {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.flexbox-v {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.flexbox-h {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.table {
  display: table;
  width: 100%;
  table-layout: fixed;
}
.table > * {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.small {
  font-size: 12px;
}
.pd-15 {
  padding: 15px;
}
.fl-r {
  float: right;
}
.abs-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/*用户头像*/
.user-header {
  min-height: 150px;
  padding: 30px;
  text-align: center;
  font-size: .35rem;
  color: #266dbf;
}
.user-header img {
  width: 2rem;
  height: 2rem;
  border-radius: 100%;
  display: inline-block;
  margin-bottom: 20px;
}
/* 自定义组件样式*/
#app {
  height: 100%;
}
.router-fade-enter-active,
.router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter,
.router-fade-leave-active {
  opacity: 0;
}



