
.search-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px;
  border: 1px solid #555;
  border-radius: 15px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 5px;
}
.search-box > input {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border: none;
  outline: none;
  margin-left: 10px;
}
.search-box.inner {
  background-color: #fff;
  border-radius: 5px;
}
.search-page .back {
  position: absolute;
  top: 20px;
  left: 20px;
  color: #fff;
}
.search-panel {
  margin: 10px;
  color: #fff;
}
.search-panel .name {
  margin-bottom: 10px;
}
.search-panel a {
  display: inline-block;
  padding: 5px;
  background-color: #f1f1f1;
  border-radius: 3px;
  color: #000;
  margin: 0 10px 10px 0;
}
.m-popup-mask {
  background-color: rgba(0, 0, 0, 0.6);
}
.m-popup,
.m-popup .popup-content {
  background-color: transparent;
}
.popup-content {
  padding: 80px 30px 30px;
}



