
.course-img {
  height: 200px;
}
.course-title {
  padding: 5px 10px;
}
.course-title .name {
  font-size: 18px;
  line-height: 1.5;
}
.course-title .author {
  font-weight: bold;
  margin-bottom: 10px;
  text-align: right;
}
.course-start {
  height: 60px;
}
.course-start .money {
  color: orange;
  font-size: 16px;
}
.course-start .start-btn {
  background-color: #266dbf;
  color: #fff;
  font-size: 16px;
}
.course-accordion .accordion-title {
  min-height: 1.2rem;
}
.course-content,
.course-flow {
  padding: 20px;
}
.course-flow .item {
  padding: 0 0 10px 20px;
  position: relative;
}
.course-flow .item:before {
  position: absolute;
  content: '';
  display: block;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background-color: #999;
}
.course-flow .item:after {
  position: absolute;
  content: '';
  display: block;
  top: 0;
  left: -1px;
  width: 4px;
  height: 4px;
  background-color: #000;
  border-radius: 100%;
}
.course-flow .title {
  position: relative;
  font-size: .3rem;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.course-flow .content {
  padding: 10px 0;
}
.course-content .course-teacher {
  -webkit-box-flex: 0;
      -ms-flex: 0;
          flex: 0;
  padding: 0 .5rem 0 0;
}
.course-content .course-teacher img {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
}



